body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navpage-area .navpage-list{
    padding:0px;
}
.navpage-area .navpage-item{
    list-style-type:none; 
    cursor:pointer;
    padding:10px 15px;
}
.navpage-area .navpage-item-icon{
    height:25px;
    display:inline;
}
.navpage-area .navpage-item-name{
    display:inline;
    margin:0px 15px;
}
.navpage-logo-icon{
    height:48px;
    margin-bottom: 14px;
    margin-right: 6px;
}
.navpage-logo-title{
    font-size:32px;
    line-height:40px;
    display:inline-block;
    padding-top:10px;
}
.navpage-logo{
    margin:10px 10px;
}
.homepage .ant-row{
    margin-bottom:25px;
}
.homepage .homepage-panel  .panel-title{
    margin-left:30px;
    margin-bottom:10px;
    margin-top:10px;
}
.user-panel {
    float:right;
    cursor:pointer;
}
.user-panel .user-panel-name{
    padding-left:8px;
}
.account-panel-menu-area{
    margin-top:-15px;
}
.app-content{
    height:100%;
}
.footer{
    padding: 0px;
    height:20px;
    min-height:20px;
}
.header{
}
.content{
    padding:15px;
    height:730px;
}
.login-page{
    margin-top:80px;
}
.login-page .ant-btn{
    float:right;
}
.login-page .login-logo-icon{
    height:48px;
    margin-bottom: 14px;
    margin-right: 6px;
}
.login-page .login-logo-title{
    font-size:28px;
    line-height:40px;
    display:inline-block;
    padding-top:16px;
}
.login-page .login-logo{
    margin:10px 10px;
}
#root{
    height:100%;
}
.App{
    height:100%;
}
.header{
    background:rgb(230,232,234)
}
.sider{
    background: rgb(255,255,255)
}
